import styled from 'styled-components';
import React, { useState } from 'react';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faBars, faXmark, 
    faTwitter as fabTwitter,
    faInstagram as fabInstagram,
    faDiscord as fabDiscord,} from '@fortawesome/free-solid-svg-icons';
import Navbar from './navbar2';
import { CSSTransition } from "react-transition-group";
import { faDiscord, faInstagram, faTwitter } from '@fortawesome/free-brands-svg-icons';
import { useNavigate } from "react-router-dom";
import DragDown, { DropDownannimation } from "./Dropdownanimaition"


const Nav = styled.div`
  position: fixed;
  padding: 20px;
  padding-right: 30px;
  padding-top: 30px;
  right: 0;
  z-index: 999;
  font-size: 2rem;
  cursor: pointer;

  transition: color 2s ease;

  @media screen and (min-width: 1024px) and (max-width: 1920px) {
    display: none;
  }

  @media screen and (max-width: 1023px) {
    display: block;
  }

`;
const MoreContainer = styled.div`
`;
const SideMenu = styled.div`
  position: fixed;
  top: 0;
  right: 0;
  height: 100%;
  width: 100%;
  background-color: rgba(0,0,0,0.6);

  color: #fff;
  z-index: 998;

  @media screen and (min-width: 1024px) and (max-width: 1920px) {
    display: none;
  }
  &.menu-enter {
    transform: translateX(300%);
  }

  &.menu-enter-active {
    transform: translateX(0);
    transition: transform 0.3s ease;
  }

  &.menu-exit {
    transform: translateX(0);
  }

  &.menu-exit-active {
    transform: translateX(300%);
    transition: transform 0.3s ease;
  }
`;

const Menu = styled.div`
font-size: 27px;
font-family: "MSBee-Black";
position: relative;
text-decoration:none;
top: 15%;
padding-left: 60px;

hr{
    margin-left: 0px;
    width: 92%;
    opacity: 0.3;
    margin-top: 20px;

}
  div {
  

   
  }
`;
const BlurBackground = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  backdrop-filter: blur(5px); 
  z-index: 997;
  display: ${({ isOpen }) => (isOpen ? 'block' : 'none')}; 
`;

function NavbarAnimation() {
    const [isOpen, setIsOpen] = useState(false);
    const [more, SetMore] = useState(false);

    const toggleSide = () => {
      setIsOpen((prevOpen) => !prevOpen);
    };

    const navigate = useNavigate();

  
    const goToBeanz = () => {
      navigate("/Beanz");
    };
  
    const goToWorld = () => {
      navigate("/World");
    };
    const goToGallery = () => {
      navigate("/Gallery");
    };


  return (
    <>
      <Nav>
 
            <FontAwesomeIcon isOpen={isOpen}
          setIsOpen={setIsOpen}
          onClick={toggleSide}
          icon={isOpen ? faXmark : faBars} style={{ color: '#ffffff' }} />
      </Nav>
      <CSSTransition in={isOpen} timeout={300} classNames="menu" unmountOnExit>
        <SideMenu>
          <Menu>
            <div  onClick={goToBeanz}
            style={{cursor:"pointer",
            color:"rgba(0,70,90",}}>BEANZ </div>
          <hr/>
         <div  onClick={goToWorld} style={{cursor:"pointer"}}>WORLD</div>
         <hr/>
         <div style={{cursor:"pointer"}}>GALLERY</div>
         <hr/>
        <MoreContainer  onClick={() => {
                SetMore(!more);
            
              }}> <div  style={{cursor:"pointer",
              display:"block"}}>MORE</div>      {more && <DropDownannimation/>}</MoreContainer>
         <hr/>
         <a href='http://www.twitter.com' style={{color:"white",
     textDecoration:"none", cursor:"pointer"}}><FontAwesomeIcon style={{position:"relative",}} icon={faTwitter}/>   TWITTER </a>
         <hr/>
         <a href='http://www.instargram.com'  style={{color:"white", textDecoration:"none", cursor:"pointer"}}> <FontAwesomeIcon style={{position:"relative",}}icon={faInstagram}/>   INSTAGRAM </a>
         <hr/>
         <a href='http://www.discord.com'  style={{color:"white", textDecoration:"none", cursor:"pointer"}}><FontAwesomeIcon style={{position:"relative",}}icon={faDiscord}/>   DISCORD </a>
         <hr/>
         <div style={{
            marginTop:"30px",
            fontFamily:"MonoMedium",
        fontSize:"12px"}}>CHIRU LABS, INC  2021<br/>
MADE IN LOS ANGELES, CA<br/>
HELLO@AZUKI.COM</div>
         </Menu>
        </SideMenu>
      </CSSTransition>
      <BlurBackground isOpen={isOpen} onClick={toggleSide} />
    </>
  );
}
export default NavbarAnimation;
