import { styled, createGlobalStyle } from 'styled-components'; // createGlobalStyle import.
import React from 'react';
import Navbar from './navbar';
import { TransformWrapper, TransformComponent } from 'react-zoom-pan-pinch';
import HILUMIA from './Azuki/World/hilumia.jpg';
import robot1 from './Azuki/Beanz/robot (11).png';
import robot2 from './Azuki/Beanz/robot (7).png';
import robot3 from './Azuki/Beanz/robot (10).png';
import robot4 from './Azuki/Beanz/robot (9).png';
import { useState , useRef} from 'react';
import { normalizeArgs } from "../utils/args.utils";
import NavbarAnimation from "./navbaranimation";


const GlobalStyle = createGlobalStyle`
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  position: relative;
  overflow: hidden;
}
`;
const Container = styled.div`
  width: 100vw;
  height: 100vh;
`;

const FullScreenImage = styled.div`
  img {
    width: 100vw;
    height: 100vh;
    object-fit: cover;
  }

  .hover1 {
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    img {
      width: 100vw;
      height: 100vh;
      object-fit: cover;
    }
  }

  @media screen and (max-width: 767px) {
    img {
      width: 100vw;
      height: 100vh;
      object-fit: cover;
    }
  }
`;
const Imghover = styled.div`
  pointer-events: auto;

  img {
    position: absolute;
    top: 50%;
    left: 50%;
    width: 150px;
    height: 150px;
  }
  .robot1 {
    transform: scaleX(-1) translate(350%, -240%);
  }
  .robot2 {
    transform: translate(412%, -50%);
  }
  .robot3 {
    height: 140px;
    transform: scaleX(-1) translate(110%, 36%);
  }
  .robot4 {
    transform: translate(187%, 140%);
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    img {
      width: 130px;
      height: 130px;
    }
    .robot1 {
      transform: scaleX(-1) translate(270%, -100%);
    }
    .robot2 {
      transform: translate(212%, -215%);
    }
    .robot3 {
      height: 120px;
      transform: scaleX(-1) translate(110%, 36%);
    }
    .robot4 {
      transform: translate(157%, 140%);
    }

  }

  @media screen and (max-width: 767px) {
    img {
      width: 130px;
      height: 130px;
    }
    .robot1 {
      transform: scaleX(-1) translate(250%, -90%);
    }
    .robot2 {
      transform: translate(92%, -115%);
    }
    .robot3 {
      height: 120px;
      transform: scaleX(-1) translate(110%, 36%);
    }
    .robot4 {
      transform: translate(157%, 140%);
    }
    
  }
`;

const Reset = styled.button`
  text-align: start;
`;
const Screen = styled.div`
  transition: background-color 1s, opacity 1s;

  &.active {
    opacity: 0.3;
  }
`;

const Modal1 = styled.div`
  left: 50%;
  transform: translateX(-50%);
  bottom: 0;
  margin-bottom: 10px;
  font-size: 15px;
  position: absolute;
  height: 200px;
  width: 95%;
  background-color: rgba(47, 79, 79, 1);
  color: rgba(255, 255, 255, 0.7);
  border-radius: 30px;
  display: ${(props) => (props.showModal ? 'block' : 'none')};
  padding: 20px 30px;

  div {
    float: right;
    width: 1050px;
  }
  h3 {
    color: white;
    font-size: 40px;
    margin: 10px 0;
  }

  img {
    position: absolute;
    float: left;
    width: 650px;
    transform: scaleX(-1);
  }
  .mo1 {
    top: -260%;
    left: 5%;
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    height: 200px;
    width: 80%;
    left: 50%;
    bottom: 0;
    transform: translate(-50%, -10%);
    div {
      width: 650px;
      margin: auto 60px;
    }
    img {
      margin-top: 150px; 
      width: 350px;
    }
  }

  @media screen and (max-width: 767px) {
    height: 230px;
    width: 80%;
    transform: translate(-50%, -10%);
    font-size: 13px;
    div {
      width: 450px;
      margin: 0 30px 40px 0;
    }
    img {
      margin-top: 180px; 
      width: 350px;
    }
  }

`;
