export const Hilumia = (args) => {
  const transformWrapperRef = useRef();
  const [modal, setModal] = useState(false);
  const [modal2, setModal2] = useState(false);
  const [modal3, setModal3] = useState(false);
  const [modal4, setModal4] = useState(false);


  const [containerActive, setContainerActive] = useState(false);

  const handleRobot1Click = () => {
    setModal(true);
    setContainerActive(true);
    setModal2(false);
    setModal3(false);
    setModal4(false);
  };

  const handleRobot2Click = () => {
    setModal2(true);
    setContainerActive(true);
    setModal(false);
    setModal3(false);
    setModal4(false);
  };

  const handleRobot3Click = () => {
    setModal3(true);
    setModal(false);
    setModal2(false);
    setModal4(false);
    setContainerActive(true);
  };

  const handleRobot4Click = () => {
    setModal4(true);
    setModal(false);
    setModal2(false);
    setModal3(false);
    setContainerActive(true);
  };

  const handleModalClose = () => {
    setModal(false);
    setModal2(false);
    setModal3(false);
    setModal4(false);
    setContainerActive(false);
    if (transformWrapperRef.current) {
      transformWrapperRef.current.resetTransform();
    }
  };
  const handleScreenClick = () => {

    if (modal || modal2 || modal3 || modal4) {
      handleModalClose();
    }
  };

  return (
    <>
      <NavbarAnimation/>
      <Navbar />
      <GlobalStyle />
      <Container>
        <Screen
        onClick={handleScreenClick} 
        className={containerActive ? 'active' : ''}>
          <TransformWrapper
           ref={transformWrapperRef}
            initialScale={1}
            minScale={1}
            maxScale={2}
            disablePadding={true}
            {...normalizeArgs(args)}
          >
                    {({ zoomToElement, resetTransform }) => (
            <TransformComponent>
              <FullScreenImage>
                <img src={HILUMIA} alt="Hilumia"></img>
              </FullScreenImage>

              <Imghover>
                <div onClick={() => zoomToElement("element1")}>
                <img className='robot1'
                 id="element1"
                  onClick={handleRobot1Click}
                  style={{
                    pointerEvents: 'auto',
                  }}
                  src={robot1}
                  alt="robot1"
                ></img>
                </div>

                <div onClick={() => zoomToElement("element2")}>
                <img className='robot2'
                id="element2"
                  onClick={handleRobot2Click}
                  style={{
                    pointerEvents: 'auto',
                  }}
                  src={robot2}
                ></img>
                </div>

                <div onClick={() => zoomToElement("element3")}>
                <img className='robot3'
                id="element3"
                  onClick={handleRobot3Click}
                  style={{
                    pointerEvents: 'auto',
                  }}
                  src={robot3}
                ></img>
                 </div>

                 <div onClick={() => zoomToElement("element4")}>
                <img className='robot4'
                id="element4"
                 onClick={handleRobot4Click}
                  style={{
                    pointerEvents: 'auto',
                  }}
                  src={robot4}
                ></img>
                  </div>
              </Imghover>
            </TransformComponent>
            )}
          </TransformWrapper>
        </Screen>

        <Modal1 onClick={handleModalClose} showModal={modal}>
          <img className='mo1'
            src={robot1}
          ></img>
          <div>
            <h3>SUN</h3>
            <p>
              Gold BEANZ are shiny warrior guardians, rarely spotted amidst the
              lush foliage of the garden.
            </p>
            <p>
              Legends tell of a tiny gold warrior with impressive fighting
              abilities. Even though he loves the spotlight, he is a fiercely
              loyal companion who will always have your back.
            </p>
          </div>
        </Modal1>
        <Modal2 onClick={handleModalClose} showModal={modal2}>
          <img className='mo2'
            src={robot2}
          ></img>
          <div>
            <h3>ASH</h3>
            <p>
              Purple BEANZ are max chill. They are the only BEANZ that can get
              red and blue BEANZ to get along. Perfect companion for a chill
              hang.
            </p>
            <p>
              Ash is the most relaxed bean in the garden. Nothing seems to faze
              him and he's always calm in times of chaos. Others tend to envy
              his chill demeanor and find themselves drawn to his cool vibes.
            </p>
          </div>
        </Modal2>
        <Modal3 onClick={handleModalClose} showModal={modal3}>
          <img className='mo3'
            src={robot3}
          ></img>
          <div>
            <h3>FRIDA</h3>
            <p>
            For a creative few, the world is their canvas. Jelly BEANZ are often creatives, artists, writers, and musicians.
            </p>
            <p>
            Frida is a creative force who finds inspiration in the most unlikely places. Although drawing is her first love, she also has a musical side. Her art brims with color, joy, and life - just like Frida herself!
            </p>
          </div>
        </Modal3>
        <Modal4 onClick={handleModalClose} showModal={modal4}>
        <img className='mo4'
            src={robot4}
          ></img>
          <div>
            <h3>ED</h3>
            <p>
            Edamame BEANZ love to work out! They are great workout buddies, personal trainers, and endurance athletes.
            </p>
            <p>
            Ed never skips leg day. He's the happiest when working out with other edamame beefcakes at the gym, ripping sets, and getting shredded. If there's a mirror around, you bet Ed is going to check out his guns because he works hard for that bean bod!
            </p>
          </div>

        </Modal4>

      </Container>
    </>
  );
}
