import React from "react";
import styled from "styled-components";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";


const BuyContainer = styled.div`
  position: absolute;

  background-color: rgba(255, 255, 255, 0.8);
  border-radius: 5px;
left: 44.5%;
margin-top: 5px;
  color: #fff;
padding: 0px 10px 10px; 

  text-align: center;
  li {
    color: black;
    text-align: left;
  margin-top: 10px;
}

`;


export function Buy() {
  return (
  <>
  <BuyContainer>
  <li>AIDER (OS) <FontAwesomeIcon icon="fa-duotone fa-square-arrow-up-right" style={{"--fa-primary-color": "#ffffff", "--fa-secondary-color": "#ffffff",}} /></li>
  <li>ROTZ (OS) <FontAwesomeIcon icon="fa-duotone fa-square-arrow-up-right" style={{"--fa-primary-color": "#ffffff", "--fa-secondary-color": "#ffffff",}} /></li>
  </BuyContainer>
  </>

  )
  
  }