import { styled } from "styled-components";
import image1 from "./Azuki/Home/image1.png";
import earth from "./Azuki/Home/earth.png";
import water from "./Azuki/Home/water.png";
import fire from "./Azuki/Home/fire.png";
import lightning from "./Azuki/Home/lightning.png";
import ELEMENTALS from "./Azuki/Home/ELEMENTALS.png";
import NavbarAnimation from "./navbaranimation";
import Polygon from "./Azuki/Home/7fcc3695-dc37-4c45-828d-12b6234d2d75.png";
import image2 from "./Azuki/Home/image2.png";
import image3 from "./Azuki/Home/image3.png";
import image4 from "./Azuki/Home/image4.png";

import React, { useState } from "react";
import Navbar from "./navbar";

const Container = styled.div`
  position: relative;

  * {
    margin: 0;
    padding: 0;
    box-sizing: border-box;

    color: #fff;
    text-decoration: none;
    text-align: center;
  }
  .main {
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
  }
`;
const Body = styled.div`
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: -1;
  background-image: ${({ backgroundImage }) => `url(${backgroundImage})`};
  background-repeat: no-repeat;
  transition: background-image 0.2s ease;

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    background-size: cover;
  }

  @media screen and (max-width: 767px) {
    background-size: cover;
  }
  //
`;

const Content = styled.div`
  .box_1 {
    margin: 20px;
  }
  .elementals {
    margin: 20px;
    width: 700px;
  }
  hr {
    margin: auto;
    color: white;
    opacity: 0.5;
    width: 300px;
  }
  span {
    letter-spacing: 4px;
  }
  .Polygon {
    width: 40px;
    margin: 0 20px 10px 20px;
    vertical-align: middle;
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    .box_1 {
      margin: 18px;
    }
    hr {
      width: 280px;
    }
    span {
      letter-spacing: 3px;
      font-size: 0.9rem;
    }
    .Polygon {
      width: 35px;
    }
    .elementals {
      width: 500px;
    }

  }

  @media screen and (max-width: 767px) {
    .box_1 {
      margin: 16px;
    }
    hr {
      width: 270px;
    }
    span {
      letter-spacing: 3px;
      font-size: 0.8rem;
    }
    .Polygon {
      width: 30px;
    }
    .elementals {
      width: 300px;
    }
  }
  //
`;

const Hover1 = styled.img`
  width: 40px;
  background-color: rgba(255, 255, 255, 0);
  filter: opacity(1) invert(100%);
  opacity: ${(props) => (props.isHovered ? 1 : 0.5)};
  transition: opacity 0.3s;

  &:hover {
    opacity: 1;
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    width: 37px;
  }

  @media screen and (max-width: 767px) {
    width: 35px;
  }
  //
`;
<avbar />;
const Hover2 = styled.img`
  margin-left: 30px;
  width: 40px;
  background-color: rgba(255, 255, 255, 0);
  opacity: ${(props) => (props.isHovered ? 1 : 0.5)};
  filter: opacity(1) invert(100%);
  transition: opacity 0.3s;
  &:hover {
    ${Body} {
      background-image: ${({ image }) => `url(${image})`};
    }
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    width: 37px;
  }

  @media screen and (max-width: 767px) {
    width: 35px;
  }
  //
`;
const Hover3 = styled.img`
  margin-left: 30px;
  width: 40px;
  background-color: rgba(255, 255, 255, 0);
  opacity: ${(props) => (props.isHovered ? 1 : 0.5)};
  filter: opacity(1) invert(100%);
  &:hover {
    ${Body} {
      background-image: url(${image2});
    }
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    width: 37px;
  }

  @media screen and (max-width: 767px) {
    width: 35px;
  }
  //
`;
const Hover4 = styled.img`
  margin-left: 30px;
  width: 40px;
  opacity: ${(props) => (props.isHovered ? 1 : 0.5)};
  background-color: rgba(255, 255, 255, 0);
  filter: opacity(1) invert(100%);

  &:hover {
    ${Body} {
      background-image: url(${image3});
    }
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    width: 37px;
  }

  @media screen and (max-width: 767px) {
    width: 35px;
  }
  //
`;

const Nav = styled.div `
  @media screen and (min-width: 768px) and (max-width: 1023px) {
 
  }

  @media screen and (max-width: 767px) {

  }
`;

export default function Main() {
  const [backgroundImage, setBackgroundImage] = useState(image1);
  const [hover1Hovered, setHover1Hovered] = useState(false);
  const [hover2Hovered, setHover2Hovered] = useState(false);
  const [hover3Hovered, setHover3Hovered] = useState(false);
  const [hover4Hovered, setHover4Hovered] = useState(false);

  return (
    <>
    <Nav>
    <NavbarAnimation/>
      <Navbar/></Nav>
      <Container>
        <Body backgroundImage={backgroundImage}>
          <Content className="main">
            <div className="box_1">
              <span>FOUR DOMAINS </span>
              <img className="Polygon" src={Polygon} alt="Polygon" />
              <span>ONE GARDEN</span>
              <div>
                {" "}
                <img
                  className="elementals"
                  src={ELEMENTALS}
                  alt="elementals"
                ></img>
              </div>
              <hr></hr>
            </div>

            <Hover1
              src={earth}
              alt="earth"
              isHovered={hover1Hovered}
              onMouseEnter={() => {
                setBackgroundImage(image1);
                setHover1Hovered(true);
                setHover2Hovered(false);
                setHover3Hovered(false);
                setHover4Hovered(false);
              }}
            />
            <Hover2
              src={water}
              alt="water"
              isHovered={hover2Hovered}
              onMouseEnter={() => {
                setBackgroundImage(image2);
                setHover1Hovered(false);
                setHover2Hovered(true);
                setHover3Hovered(false);
                setHover4Hovered(false);
              }}
            />
            <Hover3
              src={lightning}
              alt="lightning"
              isHovered={hover3Hovered}
              onMouseEnter={() => {
                setBackgroundImage(image3);
                setHover1Hovered(false);
                setHover2Hovered(false);
                setHover3Hovered(true);
                setHover4Hovered(false);
              }}
            />
            <Hover4
              src={fire}
              alt="fire"
              isHovered={hover4Hovered}
              onMouseEnter={() => {
                setBackgroundImage(image4);
                setHover1Hovered(false);
                setHover2Hovered(false);
                setHover3Hovered(false);
                setHover4Hovered(true);
              }}
            />
          </Content>
        </Body>
      </Container>
    </>
  );
}
