import NavbarAnimation from "./navbaranimation";
import React from "react";
import { styled , createGlobalStyle} from "styled-components";
import Navbar from "./navbar";

const GlobalStyle = createGlobalStyle`
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
    color: #fff;
}

`;
const Container = styled.div`
position: relative;

h3 {
    font-size: 3.8rem;
    font-family: "MSBee-Black";
    border-bottom: 1px solid rgba(128, 128, 128, 0.5);
    line-height: 110px;
}

.slash {
    opacity: 0.2;
}

//
@media screen and (min-width: 768px) and (max-width: 1023px) {
    h3 {
        font-size: 2.8rem;
    }
}

@media screen and (max-width: 767px) {
    h3 {
        font-size: 2.5rem;
    }
  
}
`;

const Page1 = styled.div`
width: 70%;
margin: 150px auto;

//
@media screen and (min-width: 768px) and (max-width: 1023px) {
    width: 70%;
    margin: 150px 0 0 60px;
}

@media screen and (max-width: 767px) {
    width: 70%;
    margin: 150px 0 50px 60px;
}
`;

const Con = styled.div`
font-family: "MonoRegular";
margin-top: 35px;
font-size: 0.8rem;
line-height: 1.5rem;
p {
  color: #d3d3d3;
}
`;

const Poi = styled.div `
position: relative;
top: -145px;
`;

function Manifesto () {
    return (
        <>
        <GlobalStyle/>
        <Navbar/><Poi>
        <NavbarAnimation ></NavbarAnimation></Poi>
        <Container>
        <Page1>
            <h3>THE MANIFESTO <span className="slash">//</span></h3>
            <Con>
                <p>
                    Here  comes a new wave...<br/>
                    And surfing here is different.<br/>
                    Breaking down barriers.<br/>
                    Building open communities.<br/>
                    Creating magic internet money with our friends.<br/>
                    To those who don't get it, we tell them: gm.<br/>
                    They'll come around eventually.<br/>
                    <br/>
                    Here's to the ones with the courage to jump down a peculiar rabbit hole.<br/>
                    One that pulls you away from a world that's created by many and owned by few...<br/>
                    To a world that's created by more and owned by all.<br/>
                    From The Garden come the human beans that sprout into your family.<br/>
                    <br/>
                    We rise together.<br/>
                    We build together.<br/>
                    We grow together.<br/>
                    <br/>
                    Ready to take the red bean?
                </p>
            </Con>
        </Page1>
        </Container>
        </>
    )

}

export default Manifesto;