import { styled,createGlobalStyle  } from "styled-components";
import React from "react";
import Navbar from "./navbar";
import { useNavigate } from "react-router-dom";
import cloud1 from "./Azuki/World/cloud1.png";
import ALLEY from "./Azuki/World/ALLEY/ALLEY.png";
import Ruins from "./Azuki/World/ruins.png";
import hilumia from "./Azuki/World/hilumia.jpg";
import { useState } from 'react';
import NavbarAnimation from "./navbaranimation";


const GlobalStyle = createGlobalStyle`
* {
  position: relative;
}
`;

const Container = styled.div`
  width: 100vw;
  height: 160.5vh;

  * {
    padding: 0;
    margin: 0;
  }
  filter: ${({ isBlurred }) => (isBlurred ? 'blur(5px)' : 'none')};
  transition: filter 0.3s ease;
`;

const Box = styled.div`
  position: relative;
  left: 50%;
  top: 50%;
  transform: translate(-52%, -46%);
  width: 100%;
  height: 90%;
  display: grid;
  grid-template-rows: 1fr 1fr 1fr;
  grid-row-gap: 30px;

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    width: 100%;
    height: 80%;
    grid-row-gap: 20px;
  }

  @media screen and (max-width: 767px) {
    width: 90%;
    height: 80%;
    margin: 0 10px;
  }
`;
const Page = styled.div`
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr;

`;

const NumBox = styled.div`
  padding-left: 80px;
  margin-top: -40px;
  font-size: 8rem;
  font-family: "MSBee-Black";

  color: rgba(255, 255, 255);
  opacity: 0.9;
  transition: opacity 0.8s ease-in-out;

  .num4 {
    color: rgba(0, 0, 0);
    opacity: 0.1;
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    margin-top: -30px;
    padding-left: 70px;
    padding-right: 10px;
    font-size: 6rem;
  }

  @media screen and (max-width: 767px) {
    display: none;
  }
`;

const ImgBox = styled.div`
  position: relative;
  width: 1500px;
  height: 300px;
  filter: grayscale(100%);
  font-size: 3rem;
  font-family: "MSBee-Bold";
  color: rgba(255, 255, 255);

  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  &:hover {
    transition: all 1s ease-in-out;
    filter: grayscale(40%);
    opacity: 1;
  }

  span {
    width: 100%;
    height: 100%;
    opacity: 0;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);

    &:hover {
      animation: World linear 0.6s forwards;
    }
  }
  h3 {
    margin-top: 200px;
    margin-left: 50px;
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    width: 1000px;
    height: 300px;
    filter: grayscale(30%);

    span {
      width: 100%;
      height: 100%;
      opacity: 0.8;
      position: absolute;
      top: 50%;
      transform: translateY(-80%);
    }


    h3 {
      margin-top: 300px;
      margin-left: 25px;
    }
  }

  @media screen and (max-width: 767px) {
    width: 1000px;
    height: 350px;
    filter: grayscale(30%);

    span {
      width: 100%;
      opacity: 0.8;
      position: absolute;
      top: 50%;
      transform: translateY(-80%);
    }
    h3 {
      margin-top: 370px;
      margin-left: 25px;
      
    }
  }
`;

const BackgroundImage = styled.div`
  animation: clouds-loop-1 50s linear infinite;
  position: fixed;

  bottom: 0;
  width: 100%;
  height: 1800px;

  background-image: url(${cloud1});
  background-repeat: repeat-x, no-repeat;
  background-position: center;
  background-size: cover;

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    height: 120%;
    background-size: cover;
  }

  @media screen and (max-width: 767px) {
    height: 120%;
    background-size: cover;
  }
`;

const Img1 = styled.div`
  width: 100%;
  height: 100%;
  border-radius: 20px;
  position: relative;
  background-position-y: -300px;

  background-image: url(${ALLEY});
  background-repeat: no-repeat;

  background-size: 126%;
  transition: background-size 0.8s ease-in-out;

  cursor: pointer;
  &:hover {
    background-size: 130%;
    .num1 { 
      opacity: 1;
    }
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    background-position-y: -180px;
  }

  @media screen and (max-width: 767px) {
    background-position-y: -1540px;
  }
`;
const Img2 = styled.div`
  cursor: pointer;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  position: relative;
  background-position-y: -200px;

  background-image: url(${Ruins});
  background-repeat: no-repeat;

  background-size: 126%;
  transition: background-size 0.8s ease-in-out;

  &:hover {
    background-size: 130%;
  }
`;

const Img3 = styled.div`
background-position-y: -130px;
  cursor: pointer;
  width: 100%;
  height: 100%;
  border-radius: 20px;
  position: relative;
  &:hover {
    background-size: 120%;
  }

  background-image: url(${hilumia});
  background-repeat: no-repeat;

  background-size: 116%;
  transition: background-size 0.8s ease-in-out;


`;


const Con1 = styled.div`
  position: absolute;
  bottom: 0%;
  margin: 20px;
  margin-left: 30px;
  opacity: 0;
  width: 100%;
  height: 100%;
  &:hover {
    animation: World 0.6s linear forwards;
  }
`;
const Con2 = styled.div`
  position: absolute;
  bottom: 0%;
  margin: 20px;
  margin-left: 30px;
  width: 100%;
  height: 100%;
  opacity: 0;
  &:hover {
    animation: World 0.6s linear forwards;
  }
`;
const Con3 = styled.div`
  position: absolute;
  bottom: 0%;
  margin: 20px;
  margin-left: 30px;
  opacity: 0;
  width: 100%;
  height: 100%;
  &:hover {
    animation: World 0.6s linear forwards;
  }
`;
export function World() {
  const [isHovered1, setIsHovered1] = useState(false);
  const [isHovered2, setIsHovered2] = useState(false);
  const [isHovered3, setIsHovered3] = useState(false);
  const navigate = useNavigate();
  const [isBlurred, setIsBlurred] = useState(false);
  const goToAlley = () => {
    navigate("/Alley");
  };

  const goToRuin = () => {
    navigate("/Ruin");
  };

  const goToHilumia = () => {
    navigate("/Hiliumia");
  };
  const toggleBlur = () => {
    setIsBlurred((prevBlurred) => !prevBlurred);
  };
  return (
    <>
      <Navbar />
      <NavbarAnimation onClick={toggleBlur}/>
      <GlobalStyle/>
      <Container isBlurred={isBlurred}>
        <BackgroundImage />
        <Box>
          <Page>
            <NumBox>
              <h3 className="num1" style={{ opacity: isHovered1 ? 1 :0.3 }}>01</h3>
            </NumBox>

            <ImgBox onClick={goToAlley}>
              <Img1 onMouseEnter={() => setIsHovered1(true)}
                onMouseLeave={() => setIsHovered1(false)}>
                <span>
                  <h3> UTOPIA</h3>
                </span>
              </Img1>
            </ImgBox>
          </Page>
          <Page>
            <NumBox>
              <h3 className="num2" style={{ opacity: isHovered2 ? 1 :0.3 }}>02</h3>
            </NumBox>
            <ImgBox onClick={goToRuin}>
              <Img2 onMouseEnter={() => setIsHovered2(true)}
                onMouseLeave={() => setIsHovered2(false)}>
                <span>
                  <h3> EDEN</h3>
                </span>
              </Img2>
            </ImgBox>
          </Page>
          <Page>
            <NumBox>
              <h3 className="num3" style={{ opacity: isHovered3 ? 1 :0.3 }}>03</h3>
            </NumBox>
            <ImgBox>
              <Img3 onClick={goToHilumia}
              style={{
           
              }}
              onMouseEnter={() => setIsHovered3(true)}
                onMouseLeave={() => setIsHovered3(false)}>
                <span>
                  <h3> NEONN</h3>
                </span>
              </Img3>
            </ImgBox>
          </Page>
          <Page>
            <NumBox>
              <h3 className="num4">04</h3>
            </NumBox>
            <ImgBox
              style={{
                backgroundColor: "black",
                opacity: "0.6",
                borderRadius: "20px",
              }}
            >
              <h3></h3>
            </ImgBox>
          </Page>
        </Box>
      </Container>
    </>
  );
}
