export function Beanz() {
  // 
  const control0 = useAnimationControls();
  const control1 = useAnimationControls();
  const control2 = useAnimationControls();
  const control3 = useAnimationControls();
  let isDown = true;
  let prevScrollY = 0;
  //

  const [scrollY, setScrollY] = useState(0);
  const [activeScrollBoxes, setActiveScrollBoxes] = useState([
    false,
    false,
    false,
    false,
  ]);
  const page2Ref = useRef(null);
  const page3Ref = useRef(null);
  const page4Ref = useRef(null);

  const handleGoToPage2 = () => {
    const page2Height = page2Ref.current.offsetTop;
    window.scrollTo({
      top: page2Height,
      behavior: 'smooth',
    });
  };

  const handleGoToPage3 = () => {
    const page3Height = page3Ref.current.offsetTop;
    window.scrollTo({
      top: page3Height,
      behavior: 'smooth',
    });
  };
  const handleScroll = () => {
    setScrollY(window.scrollY);
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  useEffect(() => {
    const page1Start = 0;
    const page2Start = 700;
    const page3Start = 1400;
    const page4Start = 2100;
    const scrollBoxStates = [
      scrollY >= page1Start && scrollY < page2Start,
      scrollY >= page2Start && scrollY < page3Start,
      scrollY >= page3Start && scrollY < page4Start,
      scrollY >= page4Start,
    ];

    setActiveScrollBoxes(scrollBoxStates);
  }, [scrollY]);
  // 
  // const [isHidden, setIsHidden] = useState(false);
  const [isFixed, setIsFixed] = useState(true);
  //

  //  
  const [isAnimating, setIsAnimating] = useState(false);
  //

  const [currentPage, setCurrentPage] = useState(1);
  const [lastPage, setlastPage] = useState(12);

  const [animate, setAnimate] = useState(false);
  const [pervanimate, setpervAnimate] = useState(false);
  const [boxContent, setBoxContent] = useState({
    image: ROTZ_IMAGES,
    text: ROTZ_CONTENT,
  });

  const [BoxContent, setBOXContent] = useState({
    text: ROTZ_CONTENT2,
  });

  const [ContentName, setContentName] = useState({
    text: ROTZ_NAME,
  });

  const [rotzboxColor, setRotzboxColor] = useState(Box_color[0]);
  const handleNextPage = () => {
    if (isAnimating) return;
    //  
    setIsAnimating(true);
    //
    setAnimate(true);
    setRotzboxColor(Box_color[currentPage]);
    setTimeout(() => {
      setCurrentPage(currentPage + 1);
      //  
      setIsAnimating(false);
      //
      setAnimate(false);
    }, 500);
  };

  const handlePrevPage = () => {
    if (isAnimating) return;
    //  
    setIsAnimating(true);
    //
    setpervAnimate(true);
    setRotzboxColor(Box_color[currentPage - 2]);
    setTimeout(() => {
      setCurrentPage(currentPage - 1);
      //  
      setIsAnimating(false);
      //
      setpervAnimate(false);
    }, 500);
  };

  // 
  useEffect(() => {
    const handleScroll = () => {
      const scrollY = window.scrollY;
      console.log(scrollY);

      //  fixed
      if (scrollY >= 1880) {
        setIsFixed(false);
      } else {
        setIsFixed(true);
      }
      //

      if (scrollY >= 0 && scrollY < 200) {
        control0.start('start');
      } else if (scrollY >= 300 && scrollY < 1000) {
        control0.start('end');
        control1.start('start');
      } else if (scrollY >= 1000 && scrollY < 1900) {
        control1.start('end');
        control2.start('start');
      } else if (scrollY >= 2000 && scrollY < 3000) {
        control2.start('end');
        control3.start('start');
      } else if (scrollY >= 3000) {
        control3.start('end');
      }
      prevScrollY = scrollY;
    };

    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, [control0, control1, control2, control3]);
  //

  useEffect(() => {
    const updateBoxContent = () => {
      const index = currentPage - 1;
      setBoxContent({
        image: ROTZ_IMAGES[index],
        text: ROTZ_CONTENT[index],
      });
      setBOXContent({
        text: ROTZ_CONTENT2[index],
      });
      setContentName({
        text: ROTZ_NAME[index],
      });
    };

    updateBoxContent();
  }, [currentPage]);

  return (
    <>
        <NavbarAnimation/>
      <Nav>
        <div className="Navbar">
          <Navbar />
        </div>
      </Nav>

      <Container>
        <ScorllBox
          active={activeScrollBoxes[0]}
          style={{
            top: '260px',
          }}
        />
        <ScorllBox
          active={activeScrollBoxes[1]}
          style={{
            top: '380px',
          }}
        />
        <ScorllBox
          active={activeScrollBoxes[2]}
          style={{
            top: '500px',
          }}
        />
        <ScorllBox
          active={activeScrollBoxes[3]}
          style={{
            top: '620px',
          }}
        />

        <Page1>
          <BackgroundImage />

          <Contents1 variants={textVariant} animate={control0}>
            <img className="bsticker" src={Bsticker} alt="Beanz_Sticker" />
            <Header>
              FROM THE GARDEN<br></br>TO THE ALLEY
            </Header>
            <div className="con">
              A small species that sprouts from the dirt in the garden. While
              they're earnestly driven by the desire to help their Azuki
              friends, some BEANZ simply can't resist the allure of the alley...
            </div>

            <Box
              style={{
                backgroundColor: 'black',
                cursor: 'pointer',
              
              }}
            >
              {' '}
              <a  onClick={handleGoToPage2} href="#Box1"> A TRUSTY SIDEKICK &nbsp; &#8595; </a>
            </Box>
            <Box>
              {' '}
              <a href="#Box2">VIEW GALLERY &nbsp; &#8594; </a>
            </Box>
            <Box>
              {' '}
              <a href="#Box3">OPEANSEA &nbsp; &#8594; </a>
            </Box>
          </Contents1>
        </Page1>

        <Page2 ref={page2Ref}>
          <BackgroundImage />

          <Contents2 variants={textVariant} animate={control1}>
            <img className="bsticker" src={Bsticker} alt="Beanz_Sticker" />
            <Header>
              &#40;ALMOST&#41; ALWAYS
              <br />
              BY YOUR SIDE
            </Header>
            <div className="con">
              Some like to be a sidekick, but others enjoy kicking it alone.
            </div>

            <Box>
              <a href="#Box1">FOR BETTER OR WORSE &#8595; </a>
            </Box>
          </Contents2>
        </Page2>

        <Page3 ref={page3Ref}>
          <BackgroundImage />

          <Contents3 variants={textVariant} animate={control2}>
            <img className="bsticker" src={Bsticker} alt="Beanz_Sticker" />
            <Header>
              FOR BETTER...
              <br />
              OR WORSE
            </Header>
            <div className="con">
              Being helpful is in their DNA, but some have a slightly different
              definition of &#34;helpful&#34;...
            </div>

            <Box>
              <a href="#Box1">MEET THE SQUAD &#8595; </a>
            </Box>
          </Contents3>

          <ChracterImage isFixed={isFixed}>
            {' '}
            <img src={Character} height={window.innerHeight} alt="Character" />
          </ChracterImage>
        </Page3>

        <Page4>
          <BackgroundImage />

          <Rotzbox
            variants={textVariant}
            animate={control3}
            className={`Rotzbox ${currentPage === 6 ? 'rotate-animation' : ''}`}
            rotate={currentPage === 6 ? 5 : undefined}
            style={{ backgroundColor: rotzboxColor }}
          >
            <img className="bsticker" src={Bsticker} alt="Beanz_Sticker" />
            <StyledRotzbox>
              <AnimatedRotzbox
                className={`robot ${pervanimate ? 'slide-enter' : ''}`}
              >
                <img
                  className={`robot ${animate ? 'slide-enter' : ''}`}
                  src={boxContent.image}
                  alt="rotz_robot"
                />
              </AnimatedRotzbox>
            </StyledRotzbox>
            <Rotzcon>
              <AnimatedRotzbox
                className={`Rotzcon ${pervanimate ? 'slide-enter' : ''}`}
              >
                <Name className={`Rotzcon ${animate ? 'slide-enter' : ''}`}>
                  <h1>{ContentName.text}</h1>
                </Name>
              </AnimatedRotzbox>

              <ROTZBOX>
                {currentPage !== 0 && (
                  <AnimatedRotzbox
                    className={`Rotzcon ${pervanimate ? 'slide-enter' : ''}`}
                  >
                    <Rotzinbox
                      className={`Rotzcon ${animate ? 'slide-enter' : ''}`}
                    >
                      <p>{boxContent.text}</p>
                    </Rotzinbox>
                  </AnimatedRotzbox>
                )}

                <p>
                  <AnimatedRotzbox
                    className={`Rotzcon ${pervanimate ? 'slide-enter' : ''}`}
                  >
                    <div className={`Rotzcon ${animate ? 'slide-enter' : ''}`}>
                      {BoxContent.text}
                    </div>
                  </AnimatedRotzbox>
                </p>
              </ROTZBOX>

              <ROTZBOX>
                {currentPage !== 1 && (
                  <ArrowL>
                    <FontAwesomeIcon
                      className="arrow"
                      onClick={handlePrevPage}
                      icon={faChevronLeft}
                    />
                  </ArrowL>
                )}
              </ROTZBOX>

              <ROTZBOX>
                {currentPage !== lastPage && (
                  <ArrowR>
                    <FontAwesomeIcon
                      onClick={handleNextPage}
                      icon={faChevronRight}
                    />
                  </ArrowR>
                )}
              </ROTZBOX>
            </Rotzcon>
          </Rotzbox>
        </Page4>
      </Container>
    </>
  );
}
