import React, { useState, useEffect, useRef } from "react";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faLanguage, faAngleDown } from "@fortawesome/free-solid-svg-icons";
import {
  faTwitter as fabTwitter,
  faInstagram as fabInstagram,
  faDiscord as fabDiscord,
} from "@fortawesome/free-brands-svg-icons";
import { styled } from "styled-components";
import logo from "./Azuki/Beanz/Aider.svg";
import ROTZBack from "./Azuki/Beanz/ROTZBack copy.svg";
import { useNavigate } from "react-router-dom";
import { keyframes } from "styled-components";
import { DropDown } from "./Dropdown";
import { Buy } from "./Buy";
import { Twitter } from "./Twitter";
import { Instar } from "./instar";

const Nav = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 40px;
  padding: 30px 50px;
  text-align: center;
  z-index: 999;
  // background-color: rgba(0, 0, 0, 0.3);
  @media screen and (min-width: 0x) and (max-width: 1023px) {

}

@media screen and (max-width: 1023px) {
  .nav_right {
  display: none;
 }
}
`;

const fadeIn = keyframes`
  from {
    background-color: rgba(0, 0, 0, 0.1);
  }
  to {
    background-color: rgba(0, 0, 0, 0.3);
  }
`;

const Logo = styled.div`
  .logo {
    position: fixed;
    display: flex;
    justify-content: center;
    align-items: center;
    width: 77px;
    height: 28px;
    background-color: rgba(47, 66, 134, 0.8);
    border-radius: 5px;
    top: 0;
    left: 0;
    margin: 40px 60px;
    cursor: pointer;
    transition: background-color 0.2s ease-in-out;
    &:hover {
      animation: ${fadeIn} 0.2s forwards;
    }
  }


  .nav_right {
    position: fixed;
    top: 0;
    right: 0;
    margin: 10px;
    padding: 30px 50px;
  }

  .nav_right img {
    height: 30px;
  }
  .nav_right .beanz {
    display: inline-block;
    height: 30px;
    background-color: rgba(0, 0, 0, 0.1);
    border: none;
    border-radius: 3px;
    font-size: 0.6rem;
    margin-left: 8px;
    overflow: hidden;
    padding: 0px 18px;
    transition: background-color 0.2s ease-in-out;
    &:hover {
      animation: ${fadeIn} 0.2s forwards;
    }
  }
  .nav_right a:not(.beanz) {
    display: inline-block;
    text-decoration: none;
    background-color: rgba(0, 0, 0, 0.1);
    color: black;
    border: none;
    border-radius: 3px;
    text-align: center;
    font-size: 0.6rem;
    margin-left: 8px;
    padding: 8px 18px;
    overflow: hidden;
    transition: background-color 0.2s ease-in-out;
    &:hover {
      animation: ${fadeIn} 0.2s forwards;
    }
  }
`;

const DownContainer = styled.span`
  text-align: center;
  li {
    color: black;
    text-align: left;
    margin-top: 10px;
    list-style-type: none;
    font-family: "MonoRegular";
    font-size: 0.7rem;
  }
`;

export function Navbar(props) {
  const [more, SetMore] = useState(false);
  const [buy, setBuy] = useState(false);
  const [twitter, setTwitter] = useState(false);
  const [instar, setinstar] = useState(false);
  const navigate = useNavigate();

  const goToMain = () => {
    navigate("/");
  };

  const goToBeanz = () => {
    navigate("/Beanz");
  };

  const goToWorld = () => {
    navigate("/World");
  };

  return (
    <Nav>
      <Logo>
        <div className="logo">
          <img src={logo} alt="logo" width={70} onClick={goToMain} />
        </div>

        <div className="nav_right">
          <div className="beanz">
            <img
              className="beenzlogo"
              src={ROTZBack}
              alt="beanz_logo"
              onClick={goToBeanz}
            />
          </div>
          <a href="" onClick={goToWorld}>
            WORLD
          </a>

          <DownContainer>
            <a
              href="#More"
              onMouseEnter={() => {}}
              onClick={() => {
                SetMore(!more);
                setBuy(false);
                setTwitter(false);
                setinstar(false);
              }}
            >
              MORE &nbsp;
              <FontAwesomeIcon icon={faAngleDown} />
            </a>

            {more && <DropDown />}

            <a
              className="Buy"
              href="#Buy"
              onClick={() => {
                setBuy(!buy);
                SetMore(false);
                setTwitter(false);
                setinstar(false);
              }}
            >
              BUY &nbsp;
              <FontAwesomeIcon icon={faAngleDown} />
            </a>

            {buy && <Buy />}

            <a
              className="twitter"
              href="#twitter"
              onClick={() => {
                setTwitter(!twitter);
                SetMore(false);
                setinstar(false);
                setBuy(false);
              }}
            >
              <FontAwesomeIcon
                icon={fabTwitter}
                style={{ fontSize: "0.8rem" }}
              />{" "}
              &nbsp;
              <FontAwesomeIcon icon={faAngleDown} />
              {twitter && <Twitter />}
            </a>

            <a
              className="instar"
              href="#instar"
              onClick={() => {
                setinstar(!instar);
                SetMore(false);
                setTwitter(false);
                setBuy(false);
              }}
            >
              <FontAwesomeIcon
                icon={fabInstagram}
                style={{ fontSize: "0.8rem" }}
              />{" "}
              &nbsp;
              <FontAwesomeIcon icon={faAngleDown} />
            </a>
            {instar && <Instar />}
          </DownContainer>
          <a href="https://discord.com/" target="_blank">
            <FontAwesomeIcon icon={fabDiscord} style={{ fontSize: "0.8rem" }} />
          </a>
          <a href="#Language">
            <FontAwesomeIcon icon={faLanguage} style={{ fontSize: "0.8rem" }} />{" "}
            &nbsp;
            <FontAwesomeIcon icon={faAngleDown} />
          </a>
        </div>

      </Logo>
    </Nav>
  );
}

export default Navbar;
