import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {

} from "@fortawesome/free-solid-svg-icons";

const TwitterContainer = styled.div`
position: absolute;

background-color: rgba(255, 255, 255, 0.8);
border-radius: 5px;
left: 56%;
top: 64px;
margin-top: 5px;

padding: 0px 10px 10px; 

text-align: center;


`;


export function Twitter() {
  return (
  <>
  <TwitterContainer>
    <a href="https://twitter.com/ROTZ865010/" target="_blank" style={{ padding: 0, margin: 0}}>
  <li>@twitter</li>
  </a>
  </TwitterContainer>
  
  </>

  )
  
  }