import { styled ,keyframes, createGlobalStyle} from 'styled-components';
import React from 'react';
import Navbar from './navbar';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import {faArrowRight } from "@fortawesome/free-solid-svg-icons";
import "./Animation.css";
import Alleysback from './Azuki/World/ALLEY/Alleyback.png';
import Alleycharacter from './Azuki/World/ALLEY/alleycharacter.png';
import { useState , useEffect} from 'react';

import NavbarAnimation from "./navbaranimation";
const GlobalStyle = createGlobalStyle`
body {
  position: relative;
  overflow: hidden;
  * {
     margin:0;
     padding:0;
    }
}
`;
const fadeIn = keyframes`
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
`;

const Container = styled.div`
width: 100vw;
height: 100vh;
overflow: hidden;
`;

const AlleyImage = styled.div`
  position: fixed;
  top: 0;
  left: 0;
  
  width: 100%;
  height: 100%;

  overflow: hidden;

  background-image: url(${Alleysback});

  background-repeat: no-repeat;
  background-size: cover;
  
  animation:  ${fadeIn} 2s forwards 1s;

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {

  }

  @media screen and (max-width: 767px) {
    
  }
`;

const AlleyCharacter = styled.div`
  position: absolute;
  left:50%;
  top: 20%;

  width: 100%;
  height: 90%;

  background-image: url(${Alleycharacter});
  background-repeat: no-repeat;
  transition: transform 0.2s ease;
  
  transform: translate(0%,-5%);

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    transform: translate(-15%,-5%);
  }

  @media screen and (max-width: 767px) {
    transform: translate(-30%,-5%);
  }
  
`;

const Cir = styled.div`
  position: absolute;
  width:100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.7);

  div{
    position: absolute;
    color: white;
    transform: translate(50px, 700px);
    font-family: "MSBee-Regular";
  }

  .CirTitle{
    opacity: 0;
   animation: ${fadeIn} 0.5s forwards 1s;
    font-size: 40px;
    color: rgba(220, 20, 70);
    font-weight: 600;
   padding-bottom: 10px;
   font-family: "MSBee-Bold";
  }

  .CirCon{
    opacity: 0;
    animation: ${fadeIn} 0.5s forwards 1.8s;
    font-size: 1rem;
   div {
    margin-left: 5px;
   }
  }

  .arr {
   font-size: 30px;
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
  div {
    transform: translate(40px, 630px);
  }
  .CirTitle{
    font-size: 30px;
  }
  .CirCon{
    font-size: 0.8rem;
    .arr {
      font-size: 20px;
    }
  }
  }

  @media screen and (max-width: 767px) {
  div {
    transform: translate(40px, 650px);
  }
  .CirTitle{
    font-size: 25px;
  }
  .CirCon{
    font-size: 0.8rem;
    .arr {
      font-size: 20px;
    }
  }

  }
`;

const Cir2 = styled.div`
  position: absolute;
  width:100vw;
  height: 100vh;
  background-color: rgba(0,0,0,0.7);
  display: none;

  div {
   position: absolute;
   color: white;
   transform: translate(50px, 700px);
   font-family: "MSBee-Regular";
   opacity: 0;
   animation: ${fadeIn} 0.5s forwards 1s;
  }

  .CirTitle2 {
   opacity: 0;
   animation: ${fadeIn} 0.5s forwards 1s;

   font-size: 40px;
   color: rgba(220, 20, 70);
   font-weight: 600;
   padding-bottom: 10px;
   font-family: "MSBee-Bold";
  }

  .CirCon {
    font-size: 1rem;
    div {
    margin-left: 5px;
    }
  }


  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    div {
      transform: translate(40px, 630px);
    }
    .CirTitle2 {
      font-size: 30px;
    }
    .CirCon {
      font-size: 0.8rem;
      .arr {
        font-size: 20px;
      }
    }
  }
  
  @media screen and (max-width: 767px) {
   div {
    transform: translate(40px, 650px);
   }
   .CirTitle2 {
     font-size: 25px;
   }
   .CirCon {
    font-size: 0.8rem;
    .arr {
      font-size: 20px;
    }
   }
  }

`;

export function Alley() {

  const [showCir, setShowCir] = useState(true); 
  const [showCir2, setShowCir2] = useState(false); 
  const [clickDisabled, setClickDisabled] = useState(false); 
  
  const handleClick = () => {
    if (clickDisabled) return; //

    if (showCir) {
      setShowCir(false);
      setShowCir2(true);

    } else if (showCir2) {
      setShowCir(false);
      setShowCir2(false);
    }
    setClickDisabled(true); 
  };


  
  useEffect(() => {
    const timeout = setTimeout(() => {
      setClickDisabled(false);
    }, 4000);

    

    return () => clearTimeout(timeout);
  }, [showCir, showCir2]);


  return (
    <>
    <Navbar/>
    <NavbarAnimation/>
      <Container  >
    
        <AlleyImage onClick={handleClick}> 
      
          <AlleyCharacter />
          <Cir style={{ display: showCir ? "block" : "none", }}>
           <div className="CirTitle">Welcome to the UTOPIA.</div>

           <br/><br/><br/>

           <span className="CirCon">
             <div>Seekers arrive for diverse motives.</div><br/>
             <div>Yet AzukiSynths assemble for a singular aim: </div><br/>
             <div>To unlock the gateway to the Utopia.</div><br/><br/>
             <div style={{cursor:"pointer",}}>   
             <FontAwesomeIcon className="arr" icon={faArrowRight} style={{color: "#fff",}} /></div>
             <br/>
           </span>
          </Cir>
          
          
          <Cir2  style={{ display: showCir2 ? "block" : "none" }}>
           <div className='CirTitle2'>Seize the Cyber Orb</div>

           <br/><br/><br/>

           <span className="CirCon">
              <div>The elusive cyber orb. </div><br/>
              <div>Witnessed by a select few, possessed by even fewer. </div><br/>
              <div>Are you prepared to seize the cyber orb?</div><br/><br/>
              <div style={{cursor:"pointer"}}>   
               <FontAwesomeIcon className="arr" icon={faArrowRight} style={{color: "#fff",}} /></div>
              <br/>
           </span>
          </Cir2> 
        </AlleyImage>
     
      </Container>

    </>
  );
}
