import NavbarAnimation from './navbaranimation copy';
import React, { useState, useEffect, useRef } from 'react';
import { styled, createGlobalStyle, keyframes } from 'styled-components';
import Navbar from './navbar2';
import map from './Azuki/More/708823.svg';
import Character from './Azuki/Beanz/1a.png';

const GlobalStyle = createGlobalStyle`
* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

`;
const GardenNavbar = styled(Navbar)`
  color: #000;
`;

const Container = styled.div`
  background-color: rgba(255, 255, 255);
  position: relative;
  width: 100vw;
  height: 300vh;

  .character {
    position: fixed;
    width: 500px;
    left: 50%;
    transform: translate(55%, 8%);
  }

  h3 {
    font-family: 'MSBee-Black';
    font-size: 3.5rem;
  }
  div {
    font-family: 'MonoRegular';
    font-size: 0.9rem;
  }
  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    .character {
      width: 350px;
      transform: translate(30%, 30%);
    }
    h3 {
      font-size: 2.5rem;
    }
    div {
      font-size: 0.8rem;
    }
  }

  @media screen and (max-width: 767px) {
    .character {
      width: 350px;
      transform: translate(18%, 30%);
    }
    h3 {
      font-size: 2.5rem;
    }
    div {
      font-size: 0.8rem;
    }
  }
`;
const Page1 = styled.div`
  position: relative;
  width: 100%;
  padding: 250px 0 0 0;

  div {
    width: 65%;
  }
  .con {
    line-height: 20px;
  }

  .cc {
    line-height: 25px;
  }
  .Page {
    margin-left: 200px;
  }
  span {
    opacity: 0.2;
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    padding: 180px 0 0 0;
    .Page {
      margin-left: 100px;
    }
    div {
      width: 70%;
    }
  }

  @media screen and (max-width: 767px) {
    padding: 150px 0 0 0;
    .Page {
      margin-left: 100px;
    }
    div {
      width: 70%;
    }
  }
`;
const ScorllBox = styled.div`
  position: fixed;
  width: 3px;
  height: 100px;
  background-color: ${({ active }) =>
    active ? 'rgba(50, 20, 180, 0.7)' : 'rgba(0, 0, 0, 0.2)'};
  margin-left: 80px;

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    margin-left: 50px;
  }

  @media screen and (max-width: 767px) {
    margin-left: 50px;
  }
`;

const Page2 = styled.div`
  position: relative;
  margin-top: 400px;

  img {
    width: 800px;
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-95%, -40%);
    opacity: 0.4;
  }

  .Page {
    margin-left: 200px;
  }
  .cc {
    margin-top: 30px;
    line-height: 30px;
  }
  .slash {
    opacity: 0.2;
  }
  .bottom {
    margin-top: 400px;
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    .Page {
      margin-left: 100px;
    }
    img {
      width: 500px;
      transform: translate(-75%, -35%);
    }
    .bottom {
      margin-top: 270px;
    }
    div {
      width: 75%;
    }
  }

  @media screen and (max-width: 767px) {
    .Page {
      margin-left: 100px;
    }
    img {
      width: 400px;
      transform: translate(-70%, -25%);
    }
    .bottom {
      margin-top: 200px;
    }
    div {
      width: 75%;
    }
  }
`;
const Page3 = styled.div`
  position: relative;
  margin-top: 400px;

  .Page {
    margin-left: 200px;
  }

  hr {
    width: 690px;
    margin: 13px;
    margin-left: 200px;
  }
  .con {
    margin-top: 30px;
    line-height: 30px;
  }
  span {
    opacity: 0.2;
  }

  //
  @media screen and (min-width: 768px) and (max-width: 1023px) {
    .Page {
      margin-left: 100px;
    }
    hr {
      width: 43%;
      margin: 13px;
      margin-left: 100px;
    }
    div {
      width: 65%;
    }
    h3 {
      width: 65%;
    }
  }

  @media screen and (max-width: 767px) {
    .Page {
      margin-left: 100px;
    }
    hr {
      width: 43%;
      margin: 13px;
      margin-left: 100px;
    }
    div {
      width: 65%;
    }
    h3 {
      width: 70%;
    }
  }
`;

const Button = styled.button`
  margin: 30px 10px;
  width: 130px;
  height: 25px;
  background-color: rgba(0, 0, 0);
  font-family: 'MonoRegular', monospace;
  color: white;
  border: none;
  border-radius: 15px;
  cursor: pointer;
  pointer-events: auto;
`;

function Garden() {
  const [scrollY, setScrollY] = useState(0);
  const [activeScrollBoxes, setActiveScrollBoxes] = useState([
    false,
    false,
    false,
  ]);
  const page2Ref = useRef(null);
  const page3Ref = useRef(null);

  const handleGoToPage2 = () => {
    const page2Height = page2Ref.current.offsetTop;
    window.scrollTo({
      top: page2Height,
      behavior: 'smooth',
    });
  };

  const handleGoToPage3 = () => {
    const page3Height = page3Ref.current.offsetTop;
    window.scrollTo({
      top: page3Height,
      behavior: 'smooth',
    });
  };
  const handleScroll = () => {
    setScrollY(window.scrollY);
  };

  useEffect(() => {
    window.addEventListener('scroll', handleScroll);
    return () => {
      window.removeEventListener('scroll', handleScroll);
    };
  }, []);

  useEffect(() => {
    const page1Start = 0;
    const page2Start = 700;
    const page3Start = 1400;

    const scrollBoxStates = [
      scrollY >= page1Start && scrollY < page2Start,
      scrollY >= page2Start && scrollY < page3Start,
      scrollY >= page3Start,
    ];

    setActiveScrollBoxes(scrollBoxStates);
  }, [scrollY]);

  const paragraphStyle = {
    background:
      'linear-gradient(104deg, hsla(247, 100%, 55%, 0) 0%, #a79cff 0%, hsla(237, 80%, 75%, .5) 5.8%, hsla(227, 100%, 75%, .1) 93%, hsla(227, 80%, 85%, .7) 96%)',
    textShadow: '0px 0px 15px hsla(211, 100%, 71%, .5), 0px 0px 18px #9eabff',
    display: 'inline-block',
    borderRadius: '5.5px',
    fontWeight: 600,
    fontSize: '14px',
  };

  return (
    <>
      <GlobalStyle />
      <GardenNavbar />
      <NavbarAnimation />
      <Container>
        <img className="character" src={Character} alt="Character" />
        <ScorllBox active={activeScrollBoxes[0]} style={{ top: '320px' }} />
        <ScorllBox active={activeScrollBoxes[1]} style={{ top: '440px' }} />
        <ScorllBox active={activeScrollBoxes[2]} style={{ top: '560px' }} />

        <Page1>
          <div className="Page">
            <h3>
            FROM THE CIRCUIT
              <br /> TO THE SYNTH GROVE  <span>//</span>
            </h3>
            <br />

            <div className="cc">
              <p>AzukiSynth emerges as a concept.</p>
              <p>A novel concept coalescing through collective efforts.</p>
              <p style={paragraphStyle}>
              A concept born for the metaverse. Forged by the community.
              </p>
            </div>

            <br />

            <div className="con">
            A compendium of 10,000 avatars, bestowing upon you membership privileges to The Synth Grove. It commences with exclusive tech-style collaborations, NFT releases, live phenomena, and myriad other revelations in the offing. Community stewardship within AzukiSynth ushers in an innovative media realm yet uncharted by the world. An AzukiSynth becomes your metaversal identity  let's construct together.
            </div>

            <br />

            <div className="con">
            The Synth Grove stands as a digital realm where art, camaraderie, and culture meld seamlessly,
              <br />
              conjuring enchantment. The boundaries between tangible and virtual domains blur, and the
established norms undergo a metamorphosis.
              <br />
              rules are being rewritten.
              <br />
            </div>

            <br />

            <div>
            Embrace the Red Pulse. //
              <Button onClick={handleGoToPage2}>We Embarked </Button>
            </div>
          </div>
        </Page1>

        <Page2 ref={page2Ref}>
          <div className="Page">
            <h3>
            OWNED BY MANY <span className="slash"> //</span>
            </h3>
            <div className="cc">
            The authentic collective is the extended AzukiSynth fellowship -<br />
              <span style={paragraphStyle}>
              a worldwide community in unison, crafting a decentralized harbinger of brands yet unseen:
              </span>{' '}
           
              <br />
              <br />
              <img
                style={{
                  filter: 'brightness(0.2) sepia(1) hue-rotate(197deg)',
                }}
                src={map}
                alt="page map"
              ></img>
              <div className="bottom">
                Fall into the
                <Button
                  onClick={handleGoToPage3}
                  style={{ width: '143px', position: 'relative', zIndex: '2' }}
                >
                  Rabbit Hole 
                </Button>
              </div>
            </div>
          </div>
        </Page2>

        <Page3 ref={page3Ref}>
          <div className="Page">
            <h3>
            INTO THE<br/> ELECTRON ABYSS
              <span> //</span>
            </h3>
            <div className="con">
            For those endowed with the audacity to plunge into an extraordinary electron abyss.<br/>
              One that detaches you from a world forged by multitudes, possessed by a scant few...
            </div>
          </div>
          <hr />
        </Page3>
      </Container>
    </>
  );
}

export default Garden;
